<template>
    <div class="row" v-if="allNews">
        <div class="col-md-4 col-sm-6 padded-v" v-for="news in allNews">
            <card  :image="news.image" :header-text="news.title" >
                <div class="label text-left">
                    <!--<small>Publicado em :  {{ fromTs(news.created_at.date).toFormat(constants.dates.BASIC_WITH_HOUR).replace(" ",' √†s ') }}</small>-->
                </div>
                <h2 class="h4">
                    {{ news.title }}
                </h2>
                <p>
                    {{news.resume}}
                </p>
                <div class="text-center padded-v">
                    <router-link class="btn btn-primary btn-lg text-upper"
                                 :to="{name:'news.show',params:{id:news.id}}">
                        <span>Ver</span>
                    </router-link>
                </div>
            </card>

        </div>
    </div>
</template>
<script>
    import Card from '../../../components/CustomCard'

    import Info from '../../../components/ElementInfo'
    import newsMixis from '../../../mixins/news'

    export default {
        mixins: [newsMixis],
        components: {
            Card,
            Info,
        },
        methods: {},

        mounted() {
            this.preload(true);
            this.getAllNews()
                .then(() => {
                    this.preload(false)
                });
        }
    }
</script>
<style scoped>
    .btn.btn-primary {
        width: 70%;
    }

    .badge {
        margin-right: 5px;
    }
</style>